import React, { Component } from 'react';
import { connect } from 'react-redux';

class CardList extends Component {
  // randomize cards
  constructor() {
    super();
    this.state = {
      currentIndex: 0,
      currentCard: '',
    };
  }

  nextIndex(currentIndex) {
    this.setState({ currentIndex: currentIndex++ });
  }

  prevIndex(currentIndex) {
    this.setState({ currentIndex: currentIndex-- });
  }

  // setCurrentCard() {
  //   this.setState({ currentCard: cards[this.state.currentIndex] });
  // }

  randomizeCards(cards) {
    let i = 0;
    let j = 0;
    let temp = null;
    for (i = cards.length - 1; i > 0; i--) {
      j = Math.floor(Math.random() * (i + 1));
      temp = cards[i];
      cards[i] = cards[j];
      cards[j] = temp;
    }
    return cards;
  }

  render() {
    let { cards } = this.props;
    cards = this.randomizeCards(cards);

    return (
      <div className="card-list">
        <div
          className="card-item box container"
          style={{ marginTop: '20vh', width: '25vw', height: '25vh' }}
        >
          {cards[this.state.currentIndex].front}
          <div className="card-flip">
            <i className="fa fa-repeat" aria-hidden="true" />
          </div>
          <div className="controller" style={{ marginTop: '7vh' }}>
            <div
              className="button"
              onClick={() => this.nextIndex(this.state.currentIndex)}
              style={{ marginRight: '13.5vw' }}
            >
              Next
            </div>
            <div className="button" onClick={() => this.prevIndex(this.state.currentIndex)}>
              Prev
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  cards: state.decksReducer.currentCards,
});
export default connect(
  mapStateToProps,
  null,
)(CardList);



